import React from 'react';
import { Link } from 'gatsby';

import logo from '../../assets/logo/ps-logo_male.svg';

class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      x: 0,
      y: 0,
      ax: 0.01,
      ay: 0.01,
      xx: 0,
    };
    this._onMouseMove = this._onMouseMove.bind(this);
  }

  _onMouseMove = e => {
    this.setState({
      x: e.pageX,
      y: e.pageY,
      ax: -(window.innerWidth / 2 - e.pageX) / 20,
      ay: (window.innerHeight / 2 - e.pageY) / 20,
      xx: window.innerWidth,
    });
  };

  render() {
    const { x, y, ax, ay, xx } = this.state;
    let xxx = false;
    if (xx > 968) {
      xxx = true;
    }
    return (
      <div>
        <div
          onMouseMove={this._onMouseMove}
          class="gradient"
          style={
            xxx
              ? {
                  background: `radial-gradient(at ${x}px ${y}px,rgba(255,255,255,.1) 0,rgba(0,0,0,.9) 45%)`,
                  opacity: `0.7`,
                }
              : {}
          }
        >
          <div className="container">
            <img
              src={logo}
              alt=""
              style={{
                position: `absolute`,
                width: `380px`,
                top: `50%`,
                left: `50%`,
                transform: `translate(-50%, -50%)`,
              }}
            />
            <div className="home">
              <div
                className="perspective-card"
                style={
                  xxx
                    ? {
                        transform: `rotateY(${ax}deg) rotateX(${ay}deg) translateZ(100px)`,
                      }
                    : {}
                }
              >
                <div
                  className="perspective-card-content"
                  style={{ opacity: `1` }}
                >
                  <p>jesteśmy polską</p>
                  <Link to="onas">firmą inżynierską</Link>
                  <p>z 10-letnim doświadczeniem w branży</p>
                  <p>
                    <Link to="automatyka">automatyki przemysłowej</Link>
                  </p>
                  <p>zajmujemy się kompleksową obsługą firm </p>
                  <p>
                    <Link to="utrzymanie_ruchu">produkcyjnych</Link>
                  </p>
                  <p>
                    <Link to="instalatorzy">i instalacyjnych</Link>
                  </p>
                  <p>w zakresie</p>
                  <Link to="">projektowania,</Link>
                  <Link to="">prefabrykacji</Link>
                  <p>oraz</p>
                  <Link to="">dostaw</Link>
                  <p>akcesoriów elektrotechnicznych</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default Home;
